.chart-statistics-content {
  height: 280px;
  overflow: auto;
  padding: 10px 22px 10px 22px;
  background: rgb(255, 255, 255);
}
.chart-statistics-content .no-data-wrapper {
    height: 250px;
}
.chart-statistics-dotted {
  margin: 5px;
  border-top: 1px dashed #dcdee5;
  height: 1px;
  overflow: hidden;
}
.chart-statistics-div {
  height: 34px;
  line-height: 34px;
  border-top: 1px dashed #dcdee5;
}
.chart-statistics-div:last-child {
    border-bottom: 1px dashed #dcdee5;
}
.chart-statistics-tool {
  min-width: 10%;
  padding-left: 20px;
  font-family: Microsoft YaHei;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  color: #737987;
  display: inline-block;
  vertical-align: top;
}
.chart-statistics-tool .tool-name {
    width: 100px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.chart-statistics-chart {
  display: inline-block;
  width: 70%;
  height: 12px;
  background-color: #3a84ff;
  vertical-align: baseline;
}
.chart-statistics-normal {
  display: inline-block;
  width: 70%;
  height: 12px;
  background-color: #c0c4cc;
  vertical-align: baseline;
}
.chart-statistics-num {
  margin-left: 15px;
  font-family: Arial;
  font-size: 12px;
  color: #63656e;
  display: inline-block;
  vertical-align: baseline;
}
